<template>
  <div class="container">
    <h1></h1>

    <div class="from-panel">
      <el-form label-position="top" ref="form" label-width="80px" :rules="rules" :model="user">
        <el-form-item label="" prop="name">
          <el-input v-model="user.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="user.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="pwd">
          <el-input type="password" v-model="user.pwd"></el-input>
        </el-form-item>
        <el-form-item label="" prop="cpwd">
          <el-input type="password" v-model="user.cpwd"></el-input>
        </el-form-item>

        <div class="oprator">
          <el-button class="submit" type="primary" @keyup.enter="submitForm('form')" @click="submitForm('form')"></el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {

  name: 'signUp',

  data() {

    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        if (this.user.cpwd !== '') {
          this.$refs.form.validateField('cpwd');
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.user.pwd) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };

    return {
      labelPosition: 'top',
      user: {
        name: '',
        email: '',
        pwd: '',
        cpwd: '',
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 7, message: ' 3  7 ', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'email', message: '', trigger: 'blur' }
        ],
        pwd: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validatePass, trigger: 'blur' },
        ],
        cpwd: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validatePass2, trigger: 'blur' },
        ]
      }
    };
  },
  mounted(){
    this.$Progress.finish();
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let user = new this.$api.SDK.User();
          user.setUsername(this.user.name);
          user.setPassword(this.user.pwd);
          user.setEmail(this.user.email);

          user.signUp().then((loginUser) => {
            this.$store.dispatch('login', loginUser); //  Vuex 
            this.$router.go(-1) // 
            this.$message.success("")
          }).catch(error => {
            console.error(error)
            this.$message.error(error.message)
          })
        } else {
          console.log('error submit!!');
          this.$message.error('');
          return false;
        }
      });
    },
  }
};
</script>

<style lang="css" scoped>
.container{
  padding: 60px 10%;
  background: #fafafa;
  min-height: calc(100vh - 180px);
}

h1{
  text-align: center;
  font-weight: 100;
  font-size: 40px;
  margin-bottom: 35px;
}

.from-panel{
  width: 50%;
  margin: 0 auto;
}

.oprator{
  margin-top: 30px;
  text-align: center;
}

</style>



